/* apply a natural box layout model to all elements, but allowing components to change */
html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

#video-controls,
.a-enter-vr {
  width: 100%;
  height: 80px;
  position: absolute;
  left: 0px;
  bottom: 0px;
  padding: 0px 10px 10px 10px;
  background: linear-gradient(to bottom, transparent 0%, rgba(0, 0, 0, 0.75) 100%); }
  #video-controls button, #video-controls input,
  .a-enter-vr button,
  .a-enter-vr input {
    float: left; }

input[type="range"] {
  margin: 0; }

#play-pause,
.a-enter-vr-button {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  border: 0;
  color: white;
  display: block;
  bottom: 10px;
  left: 10px;
  width: 10%;
  max-width: 90px;
  min-width: 70px;
  height: 60px;
  padding: 0; }

.a-enter-vr-button {
  right: 10px;
  left: auto; }

#seek-bar {
  width: 100%; }

/* Overwrite variables */
[type='range'] {
  -webkit-appearance: none;
  margin: 6px 0;
  width: 100%; }
  [type='range']:focus {
    outline: 0; }
    [type='range']:focus::-webkit-slider-runnable-track {
      background: rgba(255, 255, 255, 0.5); }
    [type='range']:focus::-ms-fill-lower {
      background: rgba(255, 255, 255, 0.5); }
    [type='range']:focus::-ms-fill-upper {
      background: rgba(255, 255, 255, 0.5); }
  [type='range']::-webkit-slider-runnable-track {
    cursor: pointer;
    height: 5px;
    transition: all .2s ease;
    width: 100%;
    box-shadow: 0px 0px 0px transparent, 0 0 0px rgba(13, 13, 13, 0);
    background: rgba(255, 255, 255, 0.5);
    border: 0px solid #cfd8dc;
    border-radius: 0px; }
  [type='range']::-webkit-slider-thumb {
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.2), 0 0 0px rgba(13, 13, 13, 0.2);
    background: white;
    border: 0px solid #eceff1;
    border-radius: 12px;
    cursor: pointer;
    height: 12px;
    width: 12px;
    -webkit-appearance: none;
    margin-top: -3.5px; }
  [type='range']::-moz-range-track {
    cursor: pointer;
    height: 5px;
    transition: all .2s ease;
    width: 100%;
    box-shadow: 0px 0px 0px transparent, 0 0 0px rgba(13, 13, 13, 0);
    background: rgba(255, 255, 255, 0.5);
    border: 0px solid #cfd8dc;
    border-radius: 0px; }
  [type='range']::-moz-range-thumb {
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.2), 0 0 0px rgba(13, 13, 13, 0.2);
    background: white;
    border: 0px solid #eceff1;
    border-radius: 12px;
    cursor: pointer;
    height: 12px;
    width: 12px; }
  [type='range']::-ms-track {
    cursor: pointer;
    height: 5px;
    transition: all .2s ease;
    width: 100%;
    background: transparent;
    border-color: transparent;
    border-width: 6px 0;
    color: transparent; }
  [type='range']::-ms-fill-lower {
    box-shadow: 0px 0px 0px transparent, 0 0 0px rgba(13, 13, 13, 0);
    background: rgba(242, 242, 242, 0.5);
    border: 0px solid #cfd8dc;
    border-radius: 0px; }
  [type='range']::-ms-fill-upper {
    box-shadow: 0px 0px 0px transparent, 0 0 0px rgba(13, 13, 13, 0);
    background: rgba(255, 255, 255, 0.5);
    border: 0px solid #cfd8dc;
    border-radius: 0px; }
  [type='range']::-ms-thumb {
    box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.2), 0 0 0px rgba(13, 13, 13, 0.2);
    background: white;
    border: 0px solid #eceff1;
    border-radius: 12px;
    cursor: pointer;
    height: 12px;
    width: 12px;
    margin-top: 0; }
